@import "../source/sass/variables";

.header {
	display: flex;
	position: fixed;
	margin: 0;
	z-index: 99;
	width: 100%;
	height: 6vh;
	background-color: $bgColor;
	align-items: center;
	justify-content: flex-end;

	.menu {
		display: inherit;
		width: 38%;
		height: 70%;
		align-items: center;
		justify-content: flex-end;
		margin: 0;

		.link {
			display: inherit;
			width: 100%;
			align-items: stretch;
			justify-content: center;
			margin: 0 8px 0 0 ;
			padding: 5px;
			font-size: 17px;
			transition: 0.3s;
			color: $colorText;
		}

		.link:hover {
			align-items: stretch;
			justify-content: center;
			border-radius: 3px;
			background-color: $primaryColor;
			color: $colorText;
		}
	}
}
